.statsWrapperDiv {
    margin: 5% auto;
    display: flex;
    /* width: 60rem; */
    /* height: 30rem; */
}

.averageStatsDiv, .leaderboardDiv {
    margin: 2.5rem auto;
    width: 35%;
    padding: 0 3rem 1rem;
    /* height: 100%; */
    background-color: white;
    border-radius: 50px;
    box-shadow: 0 2px 16px rgba(0, 0, 0, 0.5);
}

.innerLeaderboardWrapper {
    display: flex;
}

.leadersDiv {
    width: 33%;
}

.statsH1 {
    /* padding-top: 2.5%; */
    font-size: 300%;
}

.statsH2 {
    font-size: 200%;
}

.statsTitle {
    font-size: 250%;
    font-weight: bold;
}

.statsLabel, .leadersLabel {
    font-size: 125%;
    font-weight: bold;
}

.leaderP {
    font-size: 100%;
    font-weight: bold;
}

@media only screen and (max-width: 768px) {
    .statsWrapperDiv {
        flex-direction: column;
        margin: 2.5% auto;
    }
    .averageStatsDiv, .leaderboardDiv {
        width: 80vw;
        padding: 1rem;
        margin: 2.5% auto 10% auto;
    }
    .statsTitle {
        margin: 5% auto;
    }
    .innerLeaderboardWrapper {
        flex-direction: column;
    }
    .leadersDiv {
        width: 100%;
    }
    .leadersLabel {
        font-size: 175%;
    }
    .leaderP {
        font-size: 125%;
    }
}